import React from "react"
import GloboComponente from "./GloboComponente"
import { Viewer, PointPrimitiveCollection,CameraFlyTo} from "resium"
import RssFeed from "./RssFeed"
import { Cartesian3,Ion} from "cesium";
import Header from "./Header"
class Globo extends React.Component{
	constructor(){
		super()
		this.state = {
		  loading: true,
	      punti: [],
	      premuto: true,
	      id_premuto:1,
		  ip:[],
		  Rss: true,
		  Youtube: false,

	}
		
	}
	componentDidMount() {
        fetch('https://backend.ilbug.com/api/v1.0/giornali')
            .then(response => response.json())
            .then(data => {
                this.setState({
                    punti: data.map(x =>({
                    	id: x.id,
                    	nome: x.nome,
                    	longitudine: x.longitudine,
						latitudine: x.latitudine,
						url: x.url
                    }))
                })
			})
		fetch("https://ip-api.com/json/?fields=192")
        .then(response => response.json())
			.then(data => { this.setState({ip:data});fetch("https://backend.ilbug.com/api/v1.0/loc?loca="+String(data.lat)+','+String(data.lon))
			.then(response => response.json())
            .then(data => {
				let luogo=this.state.punti.findIndex(x => x.latitudine===data[0].la && x.longitudine === data[0].lon )
				luogo=this.state.punti[luogo].id
				this.setState({loading:false,id_premuto:luogo})
				})}) //this.setState({loading:false,id_premuto:data})
		this.Timeout()
			
}
	Timeout=()=>{
		setTimeout( () => {
			if(this.state.loading!==false){
				this.Timeout();
			}
		if(document.getElementById('ipl-progress-indicator')===null){
			return 0
		}
		const ele = document.getElementById('ipl-progress-indicator')
		ele.classList.add('available')
		if(ele){      	
			ele.outerHTML = ''
		}
		}, 3000 )
	}
	handleClick = (id) => {
		this.setState({premuto:false})
		this.setState({premuto:true,id_premuto:id})
  }
    handleCheck=(event) => {
		const target = event.target;
		let value = target.value;
		if(target.name === 'Youtube'){
			
			 value = target.checked
		}else if (target.name === 'Rss') {
			 value = target.checked
		}
		const name = target.name;
		this.setState({
			[name]: value    
		});

	}
	render(){
		let GloboComponentes=null
	this.state.Rss === true ?  GloboComponentes = this.state.punti.map(data => {return(<GloboComponente click={this.handleClick} key={data.id} dati={data}  idPrem={this.state.id_premuto} />)}) :  GloboComponentes= null
	Ion.defaultAccessToken = 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJqdGkiOiJlZDNjNWFkMS0yYWNhLTQyMTYtOTZiMS1jZmEyNWMwZDc1ZWYiLCJpZCI6MjM5MzEsInNjb3BlcyI6WyJhc3IiXSwiaWF0IjoxNTg1NjgyODY1fQ.WbaMpiMYZORVLwTVj9GpELCV-mepjwEjvopsuLdDjAg'
	let idpunt= this.state.id_premuto;
	idpunt= idpunt-1;	
	return(
		<div>
			<Header  eventi={this.handleCheck} rss={this.state.Rss} youtube={this.state.Youtube}/>
			<div className="Rettcont">
				{this.state.premuto ? 
				<RssFeed nome={this.state.punti[idpunt]}  /> : null }
			</div>
			<Viewer style={{position:"absolute",top:"27px",left:"0px",right:"0px",bottom:"10px"}} baseLayerPicker={false}  timeline={false} geocoder={false} homeButton={false} selectionIndicator={false} navigationHelpButton={false} animation={false} fullscreenButton={false} sceneModePicker={false} >
			      
			      { this.state.ip.lon === undefined ? null : <CameraFlyTo  duration={0.1} destination={Cartesian3.fromDegrees(this.state.ip.lon,this.state.ip.lat,1000000.0)}  once={true}/>}
				      <PointPrimitiveCollection>
				        	{GloboComponentes}
				      </PointPrimitiveCollection>
		    </Viewer>
			<ins className="adsbygoogle"
					style={{display:"flex",bottom:"0px"}}
					data-ad-client="ca-pub-2658199536475533"
					data-ad-slot="7512404495"
					data-ad-format="auto"
					data-full-width-responsive="true"></ins>
			<script>
				(adsbygoogle = window.adsbygoogle || []).push({});
			</script>
		</div>
			)		
}
}

export default Globo
